@import url(https://fonts.googleapis.com/css2?family=Stylish&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.mainBox{position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);padding:10px;max-width:1800px;width:35vw;height:250px;background-color:rgba(200,200,200,0.1);box-shadow:0 5px 15px rgba(0,0,0,0.5);border-radius:10px;font-family:'Stylish', sans-serif;text-align:center}.mainBox input{margin:auto;width:80%;height:30px;background:transparent;color:#ACFFEF;font-weight:bold;font-size:large}.mainBox input:focus{outline:none}.mainBox button{margin:auto;height:37px;background:transparent;color:#ACFFEF;font-weight:bold;font-size:large}.mainBox button:focus{outline:none}.mainBox h2{text-align:center;position:relative;margin:0 0 20px;padding:0;font-size:48px;text-transform:uppercase;z-index:2;color:#ACFFEF}.mainBox p{font-size:1rem;color:#ACFFEF}.mainBox a{color:#ACFFEF}.mainBox a:link{color:#ACFFEF;text-decoration:none}.mainBox a:visited{color:#ACFFEF}.notMain{top:10%;left:20%;width:35vw;height:100px;padding:5px}.notMain h2{font-size:2rem;margin:0}.notMain p{display:none}

.App {
  text-align: center;
}

